<template>
  <div class="personal-content-body">
    <div class="content investPersonaInfoPublish scroll-style">
      <div class="content-title clear">
        <div class="left">公告发布</div>
      </div>
      <div class="content-body clear">
        <div class="content-body-left-box left">
          <!-- 导航栏 -->
          <div class="content-body-left-box-title">
            <!-- 导航栏 -->
            <div class="clear">
              <div class="text-nav breadcrumb left">
                <el-breadcrumb separator-class="el-icon-arrow-right">
                  <el-breadcrumb-item :to="{ path: '/home/investedPublishNews' }">公告发布</el-breadcrumb-item>
                  <el-breadcrumb-item>编辑页</el-breadcrumb-item>
                </el-breadcrumb>
              </div>
            </div>
            <div class="line"></div>
          </div>
          <div class="content-body-left-box-content">
            <div class="content-part-box">
              <!-- <div class="content-part-title">Part 1：自定义发布：输入文章标题，标签，文章正文完成发布</div> -->
              <div class="content-part-body">
                <div class="form-group from-inputbox clear">
                  <div class="form-title left">
                    <em class="red">*</em>
                    <em>文</em>
                    <em>章</em>
                    <em>标</em>
                    <em>题</em>
                  </div>
                  <div class="form-input left">
                    <el-input placeholder="请输入文章标题" class="header-right-time input-position" :clearable='true' v-model="commitData.title"></el-input>
                  </div>
                </div>
                <div class="form-group from-inputbox clear">
                  <div class="form-title left">
                    <em class="red">&nbsp;</em>
                    <em>发</em>
                    <em>送</em>
                    <em>对</em>
                    <em>像</em>
                  </div>
                  <div class="form-input left">
                    <el-select placeholder="请选择公司" v-model="selectCompanyId" class="header-right-time right" @change="showCompanyInfo" filterable>
                      <el-option :key="item.id" :label="item.name" :value="item.id" v-for="item of totalCompanyList"></el-option>
                    </el-select>
                    <el-select placeholder="请选择职位" v-model="selectPositionId" class="header-right-time left selectPosition">
                      <el-option :key="item.position_id" :label="item.position" :value="item.position_id" v-for="item of positionList"></el-option>
                    </el-select>
                    <div class="content-confirm-btn btn-blue right addSelectPosition" @click="addSelectCompanyList">添加</div>
                    <!-- 循环生成信息 -->
                    <div class="show-select-companyInfo left" v-for="(item,index) of selectCompanyList" :key="index">
                      <!-- 头部 -->
                      <div class="line_company_title">
                        <span class="icon"></span>
                        <span class="companyName">{{item.companyInfo.name}}</span>
                        <span class='deleteBtn right' @click="deleteSelectCompanyList(item,index)"></span>
                      </div>
                      <!-- 职位信息列表 -->
                      <div class="line_person_list_box">
                        <div class="line_person_list" v-for="(item1,index1) of item.positionArr" :key="index1">
                          <div class="line_person_list_left">
                            <span class="icon"></span>
                            <span class="position">{{item1.position}}：</span>
                          </div>
                          <div class="line_person_list_right">
                            <span class="left" v-for="(item2,index2) of item1.name" :key="index2">{{item2}}</span>
                          </div>
                          <span class='deleteBtn right' @click="deleteOnePosition(item.positionArr,index1)"></span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="form-group from-inputbox clear">
                  <div class="form-title left">
                    <em>附</em>
                    <em>件</em>
                  </div>
                  <div class="form-input form-filebox left">
                    <div class="file-list">
                      <div class="file-item" v-for="(val,key) in commitData.adjunct" :key='key' v-if="editorconfirm">
                        <div class="file-name">
                          <div>{{key}}</div>
                        </div>
                        <a class="formgroup-file-item-btn right">
                          <input class="formgroup-file-item-btn-inputfile" type="file" multiple="multiple" @change="changeOldFile(key,$event)">替换</a>
                        <div class="file-click file-click-del" @click="deleteOldFile(key)">删除</div>
                      </div>
                      <div class="file-item" v-for="(item,index) in newFile" :key='index'>
                        <div class="file-name">
                          <div>{{item.name}}</div>
                        </div>
                        <a class="formgroup-file-item-btn right">
                          <input class="formgroup-file-item-btn-inputfile" type="file" multiple="multiple" @change="changeNewFile(index,$event)">替换</a>
                        <div class="file-click file-click-del" @click="deleteNewFile(index)">删除</div>
                      </div>
                    </div>
                    <div class="file-add">
                      <input type="file" class="file-add-input" @change="addFile($event)" multiple="multiple">
                      <i class="el-icon-plus"></i>添加文件
                    </div>
                    <!-- <el-upload class="upload-demo" drag :data="{item:conmmitSpObj.file,type:''}" action="" multiple :auto-upload="true" :show-file-list="false" :http-request="uploadfileRequest">
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                    <div class="el-upload__tip" slot="tip">不能上传音频和视频文件</div>
                  </el-upload> -->
                  </div>
                </div>
                <div class="form-group from-inputbox clear form-editorbox">
                  <div class="form-title left">
                    <em class="red">*</em>
                    <em>正</em>
                    <em>文</em>
                  </div>
                  <div class="form-input left">
                    <quill-editor class="form-editor" ref="myQuillEditor" v-model="commitData.content" :options="editorOption">
                    </quill-editor>
                  </div>
                </div>
              </div>
            </div>
            <div class="content-confirm clear">
              <div class="content-confirm-btn btn-blue" :class="{'btn-blue-disabled':!submitOk}" @click="commitClick" v-if="editorconfirm==false">发布</div>
              <div class="content-confirm-btn btn-blue" :class="{'btn-blue-disabled':!submitOk}" @click="changeCommitClick" v-if="editorconfirm==true">发布</div>
              <!-- <div class="content-confirm-btn btn-blue-border right" @click="goNewList">取消</div> -->
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  bigeventCompanyList,
  getPosition,
  newsPubNews,
  pubAffiche,
  showAffiche,
  editAffiche
} from '../../config/api'
import progressBar from '../../components/progressbar.vue'
import {
  quillEditor
} from 'vue-quill-editor'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
import _default from 'vuex';
export default {
  data() {
    return {
      //搜索数据
      oldSelectcompnay: [],
      needChangeId: true,


      editorconfirm: false,
      companyList: [],
      loading: false,
      commitData: {
        title: '',
        company: '',
        content: ''
      },

      totalCompanyList: [], //公司列表
      positionList: [], //职位列表
      selectCompanyId: '', //选中的公司的id
      selectPositionId: '', //选中的职位的Id
      selectCompanyList: [], //展示选中职位的数组；

      historyFile: [],
      newFile: [],
      currentspan: '', //标签输入框内容
      editorOption: {
        modules: {
          toolbar: [
            ['bold', 'italic'],
            //['blockquote', 'code-block'],
            [{ 'header': 1 }],
            [{ 'list': 'ordered' }, { 'list': 'bullet' }],
            /*[{ 'script': 'sub' }, { 'script': 'super' }],*/
            //[{ 'indent': '-1' }, { 'indent': '+1' }],
            //[{ 'direction': 'rtl' }],
            [{ 'size': ['small', false, 'large', 'huge'] }],
            //[{ 'header': [1, 2, 3, 4, 5, 6, false] }],
            /*[{ 'font': [] }],*/
            [{ 'color': [] }],
            [{ 'align': [] }],
            ['clean'],
            ['link', 'image']
          ],
          syntax: {
            highlight: text => hljs.highlightAuto(text).value
          }
        },
        placeholder: ''
      } //配置富文本编辑的选项
    }
  },
  components: {
    progressBar,
    quillEditor
  },
  computed: {
    submitOk: function() {
      if (this.commitData.title != '' && this.commitData.content != '') {
        return true
      } else {
        return false
      }
    }
  },
  mounted() {},
  created() {
    if (this.$route.query.aif != undefined) {
      this.editorconfirm = true;
    }
    this.initData()
  },
  methods: {
    //获取相应公司的职位信息
    showCompanyInfo() {
      getPosition({ //回去职位信息
        company_id: this.selectCompanyId,
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.positionList = res.data.payload;
          this.selectPositionId = '';
        } else {
          dialogConfirm('info', res.data.message);
        }
      })
    },
    //添加到选择职位的列表
    addSelectCompanyList() {
      if (this.selectPositionId == '') {
        dialogConfirm('info', '请先选择职位');
      } else {
        let newObj = {
          companyInfo: {},
          positionArr: [],
        }
        let selepositionObj = {}; //当前选中的职位信息
        let isNewCompany = true; //是否是新建一个公司
        let cancontinue = false;
        //获取当前的职位数组
        this.positionList.forEach(item => {
          if (item.position_id == this.selectPositionId) {
            selepositionObj = JSON.parse(JSON.stringify(item));
            if (selepositionObj.name == null || selepositionObj.name == undefined) {
              dialogConfirm('info', '该职位还未安排人员')
              cancontinue = true;
            } else {
              selepositionObj.name = selepositionObj.name.split(',');
            }
          }
        });
        if (cancontinue) {
          return false;
        }
        //如果有公司就push进去
        this.selectCompanyList.forEach(item => {
          if (item.companyInfo.id == this.selectCompanyId) {
            isNewCompany = false;
            let canPush = true;
            item.positionArr.forEach(element => {
              if (element.position_id == this.selectPositionId) {
                canPush = false;
                dialogConfirm('info', '该职位已添加!');
              }
            });
            if (canPush) {
              item.positionArr.push(selepositionObj);
            }
          }
        });

        //如果没有就新建一个对象push进去
        if (isNewCompany) {
          this.totalCompanyList.forEach(item => {
            if (item.id == this.selectCompanyId) {
              newObj.companyInfo = item;
              newObj.positionArr.push(selepositionObj);
            }
          });
          this.selectCompanyList.push(newObj);
        }

        //清空输入框
        this.selectCompanyId = '';
        this.selectPositionId = '';
        this.positionList = [];
      }
    },
    //删除选择职位的列表某一项
    deleteSelectCompanyList(item, index) {
      console.log(this.selectCompanyList);
      this.selectCompanyList.splice(index, 1);
    },
    //删除某个公司的某项职位
    deleteOnePosition(item, index) {
      console.log(item);
      console.log(index);
      item.splice(index, 1);
    },
    //获取公司列表
    initData() {
      bigeventCompanyList().then(res => {
        // console.log(res);
        if (res.data.msgNo == 1) {
          // console.log(res.data.payload);
          let data = res.data.payload
          this.totalCompanyList = data;
          // if(data.length>10){
          //   this.companyList  = data.splice(0,10);
          // }else{
          //   this.companyList =  data;
          // }

          if (this.$route.query.aif != undefined) {
            this.getNewsDetail();
          }
        }
      })
    },
    addFile(e) {
      // console.log( this.newFile);
      // console.log( e.target.files);
      for (let item of e.target.files) {
        let canPush = true;
        for (let item1 of this.newFile) {
          if (item1.name == item.name) {
            canPush = false;
            // dialogConfirm('info','选择的文件已存在');
          }
        }
        if (canPush) {
          this.newFile.push(item);
        }
      }
      e.target.value = '';
    },
    //公司搜索
    remoteMethod(query) {

      if (query !== '') {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.companyList = this.totalCompanyList.filter(item => {
            return item.name.indexOf(query) > -1;
          });
          // this.needChangeId = false;
          // console.log(this.commitData.companyId);
          if (this.companyList.length > 0) {
            // for(let companyInfo of this.companyList){

            // }
            for (let index in this.commitData.companyId) {
              for (let i of this.companyList) {
                if (i.name == this.commitData.companyId[index]) {
                  this.commitData.companyId[index] = i.id
                }
              }
            }
          }

        }, 200);
      } else {
        this.companyList = [];
      }

    },
    //获取文章详情
    getNewsDetail() {
      showAffiche({
        aif: this.$route.query.aif
      }).then(res => {
        // console.log(res.data.payload);
        if (res.data.msgNo == 1) {
          // let data = res.data.payload;
          // //console.log(data);
          // let arr = [];
          // if (typeof data.companyIdStr == 'string') {
          //   data.companyIdStr = data.companyIdStr.split(',');
          //   for (let i of data.companyIdStr) {
          //     for (let item of this.totalCompanyList) {
          //       if (item.id == i) {
          //         arr.push(item);
          //       }
          //       // break;
          //     }
          //   }
          //   // console.log(arr);
          // } else {
          //   for (let item of this.totalCompanyList) {
          //     if (item.id == data.companyIdStr) {
          //       arr.push(item);
          //     }
          //     // break;
          //   }
          //   // console.log(arr);
          // };
          // // console.log(arr);
          // this.oldSelectcompnay = arr;
          // let newArr = [];
          // for (let j of this.oldSelectcompnay) {
          //   // console.log(j);
          //   newArr.push(j.name);
          // }
          // let newObj = {
          //   title: data.info.title,
          //   companyId: newArr,
          //   content: data.info.contents
          // }

          let data = res.data.payload;
          this.commitData.title = data.info.title;
          this.commitData.content = data.info.contents;
          if (this.editorconfirm) {
            if (data.info.adjunct != '') {
              this.commitData.adjunct = JSON.parse(data.info.adjunct);
            }
          }
          let newArr = [];
          data.company.forEach(item => {
            let newObj = {
              companyInfo: {},
              positionArr: [],
            };
            newObj.companyInfo.name = item.company_name;
            newObj.companyInfo.id = item.company_id;

            if (item.position_id != null && item.position_id != '' || item.position_id != undefined) {
              item.pasition_arr.forEach(element => {
                if (element.name != '' && element.name != null) {
                  element.name = element.name.split(',')
                } else {
                  element.name = [];
                }
              });
              newObj.positionArr = item.pasition_arr;
            }

            newArr.push(newObj);
          });
          // console.log(newArr);
          this.selectCompanyList = newArr;
        }
      })
    },
    //提交文章
    commitClick() {
      if (this.submitOk) {
        let company = '';
        let param = {
          title: this.commitData.title,
          contents: this.commitData.content
        }

        if (this.selectCompanyList.length != 0) {
          company = [];
          this.selectCompanyList.forEach(element => {
            // console.log(element);
            let newObj = {};
            newObj.company_id = element.companyInfo.id;
            let positionStrArr = [];
            element.positionArr.forEach(item => {
              positionStrArr.push(item.position_id);
            });
            newObj.position_id = positionStrArr.join(',');
            company.push(newObj);
          });
          param.company = JSON.stringify(company);
        }
        // console.log(param);
        pubAffiche(param, this.newFile).then(res => {
          console.log(res);
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '发布成功')
            this.goNewList()
          }
        })
      } else {
        dialogConfirm('info', '您还有必填项没有填写！')
      }
    },

    //编辑文章
    changeCommitClick() {
      if (this.submitOk) {
        let company = '';
        let param = {
          title: this.commitData.title,
          contents: this.commitData.content,
          aif: this.$route.query.aif,
        }
        if (this.commitData.adjunct != undefined) {
          param.adjunct = JSON.stringify(this.commitData.adjunct)
        }
        if (this.selectCompanyList.length != 0) {
          company = [];
          this.selectCompanyList.forEach(element => {
            // console.log(element);
            let newObj = {};
            newObj.company_id = element.companyInfo.id;
            let positionStrArr = [];
            element.positionArr.forEach(item => {
              positionStrArr.push(item.position_id);
            });

            newObj.position_id = positionStrArr.join(',');
            company.push(newObj);
          });
          // param.company  = company;
          param.company = JSON.stringify(company);
        }
        // console.log(param);
        editAffiche(param, this.newFile).then(res => {
          // console.log(res);
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '编辑成功')
            this.goNewList()
          }
        })
      } else {
        dialogConfirm('info', '您还有必填项没有填写！')
      }
    },


    //替换文件
    changeOldFile(key, $event) {
      let oldArr = JSON.parse(JSON.stringify(this.commitData.adjunct))
      delete oldArr[key];
      // oldArr.splice(key, $event.target.files.length, "three");
      this.commitData.adjunct = oldArr;
      // delete this.commitData.adjunct[key];
      for (let item of $event.target.files) {
        this.newFile.push(item);
      }
      $event.target.value = '';
    },
    deleteOldFile(key) {
      //console.log(key);
      let oldArr = JSON.parse(JSON.stringify(this.commitData.adjunct))
      delete oldArr[key];
      this.commitData.adjunct = oldArr;
    },

    changeNewFile(index, $event) {
      this.newFile.splice(index, 1);
      for (let item of $event.target.files) {
        this.newFile.push(item);
      }
      $event.target.value = '';
    },
    deleteNewFile(index) {
      this.newFile.splice(index, 1);
    },


    //去新建新闻
    goNewList() {
      this.$router.push({
        path: '/home/investedPublishNews',
      })
    }

  }
};

</script>
